@import '../../Scss/utils/mixin';
.card {
    .card__imgcard {
        display: block;
        width: 100%;
        height: 19vw;
        object-fit: cover;
        border-radius: 20px;
        filter: drop-shadow(0px 3px 10px #d4acad);
        @include media-max(599px) {
            height: 42vw;
        }
    }
}
